import { h, Component } from "preact";

import gear from "./images/gear.svg";
import selectTriangle from "./images/select_triangle_white.svg";

class UserWidget extends Component {
  constructor(props) {
    super(props);
  }

  render({
    avatarUrl,
    displayName,
    onClick,
    onClickWheel,
    isAdmin,
    toggleProjectList,
    showProjectsMenu,
    isLoading,
    isMegaMenuOpen,
    isLinkListOpen,
    isProjectListOpen
  }) {
    if (!isLoading && !displayName) {
      return (
        <div class="user-widget">
          <a class="user-widget__log-in" href="https://accounts.platform.sh">
            Log in
          </a>
          <a
            class="user-widget__free-trial"
            href="https://accounts.platform.sh/platform/trial/general/setup"
          >
            <strong>Free Trial</strong>
          </a>
        </div>
      );
    }
    return (
      <div class="user-widget">
        {showProjectsMenu && (
          <div
            class={`user-widget__projects_menu_toggle ${isProjectListOpen && "user-widget__projects_menu_toggle--active"}`}
            onClick={toggleProjectList}
          >
            <span>Projects</span>
            <img src={selectTriangle} />
          </div>
        )}
        <div class={`user-widget__accounts_menu_toggle ${isLinkListOpen && "user-widget__accounts_menu_toggle--active"}`} onClick={onClick}>
          <img class="user-widget__avatar" src={avatarUrl} />
          <div class="user-widget__user_name" src={avatarUrl}>
            {displayName}
          </div>
        </div>
        {isAdmin && (
          <div class={`user-widget__toggle ${isMegaMenuOpen && "user-widget__toggle--active"}`} onClick={onClickWheel}>
            <img class="user-widget__toggle_img" src={gear} />
          </div>
        )}
      </div>
    );
  }
}

export default UserWidget;



