import { h, Component } from "preact";

class DropdownMenu extends Component {
  constructor(props) {
    super(props);

    this.state = {
      search: "",
      value: {}
    };

    this.onSearchChange = this.onSearchChange.bind(this);
    this.onInputValueChange = this.onInputValueChange.bind(this);
    this.onClick = this.onClick.bind(this);
    this.emptyInputText = this.emptyInputText.bind(this);
  }

  onSearchChange(e) {
    this.setState({
      search: e.target.value
    });
  }

  onInputValueChange(e) {
    this.setState({
      value: {
        value: e.target.value,
        label: e.target.value
      }
    });
  }

  emptyInputText() {
    this.setState({
      value: {
        value: "",
        label: ""
      }
    });
  }

  onClick(e, value) {
    e.stopPropagation();
    e.preventDefault();

    this.props.onChange(value);
  }

  render(
    {
      values = [],
      typeaHead = false,
      onChange,
      open,
      style,
      itemStyle,
      menuName
    },
    { search }
  ) {
    let filteredValues = [...values];

    if (typeaHead) {
      filteredValues = filteredValues.filter(v => v.label === search);
    }

    return (
      <div
        class={`user-widget__dropdown__menu ${open &&
          "user-widget__dropdown__menu--open"} ${menuName}`}
        style={style}
      >
        {filteredValues &&
        !!filteredValues.length &&
        typeaHead && (
          <input type="text" onChange={this.onSearchChange} value={search} />
        )}
        {filteredValues.map(value => (
          <a
            class="user-widget__dropdown__menu__list_item"
            style={itemStyle}
            onClick={e => this.onClick(e, value)}
          >
            {value.label}
          </a>
        ))}
        {(!filteredValues || !filteredValues.length) && (
          <div class="user-widget__dropdown__menu__input">
            <input
              value={this.state.value.value}
              onInput={this.onInputValueChange}
              type="text"
              onClick={e => e.stopPropagation()}
            />
            <div
              class="widget__dropdown__menu__input__button"
              onClick={e => {
                e.stopPropagation();
                onChange(this.state.value);
                this.emptyInputText();
              }}
            >
              Ok
            </div>
          </div>
        )}
      </div>
    );
  }
}

export default DropdownMenu;



